body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

blockquote {
  text-align: left;
  border: 2px rgba(255, 255, 255, 0.3) solid;
  border-radius: 5px;
  padding: 10px;
}

html, body {
  background-color: #223344;
  color: #fff;
  margin: 0; }

a {
  color: #008cf4;
  text-decoration: none; }

.fixed {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden; }

.background .react-logo {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  height: 60vh; }
  .background .react-logo.animate {
    left: 25%;
    top: 50%;
    -webkit-animation: logo-explode 1 4s ease;
            animation: logo-explode 1 4s ease;
    -webkit-animation-delay: 100ms;
            animation-delay: 100ms; }
  .background .react-logo.infinite {
    -webkit-animation: logo-explode infinite 4s ease;
            animation: logo-explode infinite 4s ease; }
  .background .react-logo.delayed {
    left: 50%;
    top: 25%;
    -webkit-animation-delay: 1.6s;
            animation-delay: 1.6s; }
  .background .react-logo.transition {
    opacity: 0.8;
    -webkit-transform: scale(0);
            transform: scale(0);
    transition: all 3s ease; }
  .background .react-logo.start {
    opacity: 0.3;
    -webkit-transform: scale(9);
            transform: scale(9); }

.app {
  overflow: scroll;
  position: relative;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  font-size: calc(16px + 1.6vw);
  -webkit-scroll-snap-type: y mandatory;
      -ms-scroll-snap-type: y mandatory;
          scroll-snap-type: y mandatory; }
  .app section {
    text-align: center;
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    -webkit-align-items: center;
            align-items: center;
    line-height: 1.5em;
    min-height: calc(100vh - 80px);
    margin: 20px;
    border-radius: 20px;
    padding: 40px;
    Xscroll-snap-align: start;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column; }
    .app section ul, .app section p {
      text-align: left;
      font-size: 0.8em;
      line-height: 1.5em; }
    .app section h1 {
      text-align: center; }
    .app section h3 {
      text-align: left; }
    .app section a {
      color: #223344; }
    .app section:nth-child(odd) {
      color: white; }
    .app section:nth-child(even) {
      background-color: rgba(255, 255, 255, 0.6);
      color: black; }
    .app section#welcome {
      line-height: 1.5em; }
      .app section#welcome p {
        text-align: inherit;
        font-size: 1em; }
      .app section#welcome div.next {
        font-size: 100px;
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        opacity: 0.2;
        margin-top: 200px; }
    .app section#founders ul {
      text-align: center;
      list-style-type: none; }
    .app section#founders li.person {
      position: relative;
      display: inline-block;
      background-color: #223344;
      color: white;
      font-size: 22px;
      margin: 10px;
      border-radius: 10px;
      text-align: center;
      width: 200px;
      height: 200px;
      line-height: 1.2em;
      border-radius: 50%;
      border: solid 6px #fff; }
      .app section#founders li.person a {
        padding-top: 20px;
        display: block;
        color: inherit; }
        .app section#founders li.person a img {
          position: absolute;
          top: 70px;
          left: 30px;
          width: 100px;
          height: 100px;
          border: solid 6px #fff;
          border-radius: 50%;
          margin-left: 50px;
          margin-top: 10px; }

@-webkit-keyframes logo-explode {
  from {
    opacity: 0.8;
    -webkit-transform: scale(0);
            transform: scale(0); }
  to {
    opacity: 0;
    -webkit-transform: scale(10);
            transform: scale(10); } }

@keyframes logo-explode {
  from {
    opacity: 0.8;
    -webkit-transform: scale(0);
            transform: scale(0); }
  to {
    opacity: 0;
    -webkit-transform: scale(10);
            transform: scale(10); } }

